
import TotalCount from './total_count.vue'
import RankBody from "./rank_body.vue";
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import value2name from 'vux/src/filters/value2name.js'

export default {
components: {
RankBody,
TotalCount,
XAddress
},
data() {
return {
addressData: ChinaAddressV4Data,
isPickerShow: false,
addressValue: [],
addressName: ''
};
},
props: [""],
methods: {
getName () {
return value2name(this.addressValue, ChinaAddressV4Data)
},
onPickerHide () {
this.addressName = this.getName()
}
}
};



